import { NextResponse } from "next/server";
import jwt from "jsonwebtoken";

export function middleware(req) {
  const { pathname } = req.nextUrl;

  const userToken = req.cookies.get("usertoken")?.value;
  const adminToken = req.cookies.get("admintoken")?.value;

  // üîê Protect all /signup/adminlogin/** pages
  if (pathname.startsWith("/signup/adminlogin/")) {
    if (!adminToken) {
      return NextResponse.redirect(new URL("/signup/adminlogin", req.url));
    }

    try {
      jwt.verify(adminToken, process.env.JWT_SECRET);
    } catch (err) {
      return NextResponse.redirect(new URL("/signup/adminlogin", req.url));
    }
  }

  // üîê Protect all /signup/userlogin/** pages
  if (pathname.startsWith("/signup/userlogin/")) {
    if (!userToken) {
      return NextResponse.redirect(new URL("/signup/userlogin", req.url));
    }

    try {
      jwt.verify(userToken, process.env.JWT_SECRET);
    } catch (err) {
      return NextResponse.redirect(new URL("/signup/userlogin", req.url));
    }
  }

  return NextResponse.next();
}
export const config = {
  matcher: [
    "/signup/adminlogin/:path*",
    "/signup/userlogin/:path*",
  ],
};
